<template>
  <div id="bike">
    <div class="areas" v-if="isShow">
      <div class="area" @click="createView">
        <img src="../assets/school.jpg" alt="中南财经政法大学">
        <h4>{{ area }}</h4>
      </div>
    </div>
    <div id="view"></div>
    <div class="something" v-if="!isShow">
      <h3>投放区域：{{ area }}</h3>
      <el-button type="primary" plain @click="destroyView">返回</el-button>
    </div>
  </div>
</template>

<script>
  //引入
  import {loadModules} from 'esri-loader';

  export default {
    name: 'bike',
    data () {
      return {
        isShow:true,
        center_x:114.38099,
        center_y:30.47706,
        xmin:114.37747276,
        xmax:114.3871343,
        ymin:30.47493417,
        ymax:30.47600144,
        area:'中南财经政法大学(南湖校区)'
      }
    },
    methods:{
      // 创建地图
      createView(){
        // 调用部署地址
        let options = {
          url:'https://js.arcgis.com/4.12/',
          css:'https://js.arcgis.com/4.12/esri/themes/light/main.css'
        };
        // 调用功能模块，创建地图
        loadModules(["esri/Map","esri/views/MapView","esri/layers/WebTileLayer"],options).then(([Map, MapView,WebTileLayer])=>{
        // 调用天地图的接口
          let tiledLayer = new WebTileLayer({
          urlTemplate:
            "http://{subDomain}.tianditu.gov.cn/DataServer?T=vec_w&x={col}&y={row}&l={level}&tk=9afdf2956e9d3cd48e3136dc4b122b21",
          subDomains: ["t0", "t1", "t2", "t3","t4", "t5", "t6", "t7"],
        });
        // 天地图接口的注释
          let tiledLayer_poi = new WebTileLayer({
            urlTemplate:
              "http://{subDomain}.tianditu.gov.cn/DataServer?T=cva_w&x={col}&y={row}&l={level}&tk=9afdf2956e9d3cd48e3136dc4b122b21",
            subDomains: ["t0", "t1", "t2", "t3","t4", "t5", "t6", "t7"],
          });
          // 地图，数据层，以天地图为底图
          let map = new Map({
              basemap: {
                baseLayers: [tiledLayer, tiledLayer_poi]
              }
          });
          // 地图，视图层
          this.view = new MapView({
          container: "view",//显示地图容器的id
          map: map,
          zoom: 15,//放大比例
          center: [this.center_x,this.center_y], // 经度, 纬度
          // map初始化的范围
          extent:{
            // (xmin,ymin)-左下，(xmax,ymax)-右上
            xmin:this.xmin,
            xmax:this.xmax,
            ymin:this.ymin,
            ymax:this.ymax,
            SpatialReference:{ wkid:4326 } //坐标系，4326代表地理坐标系中的GCS_WGS_1984
          }
        });
          this.isShow = !this.isShow;

        })
      },
      // 关闭地图
      destroyView(){
        if (this.view) {
        // destroy the map view
        this.view.container = null;
        this.isShow = !this.isShow;
      }
    }
  }
}
</script>

<style scoped>
#bike{
  background-image: url(../assets/blank.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  height: 770px;
}

#view{
  position: absolute;
  width: 80%;
  height: 560px;
  left: 10%;
}

.areas{
  display: flex;
  flex-direction: row;
  margin: 20px;
}

.something{
  position: absolute;
  bottom: 30px;
  left: 39%;
}

h4{
  margin: 0;
}
</style>