<template>
  <div id="trace">
    <h3>请选择您想要查看的车辆：</h3>
    <div id="trace-view"></div>
    <div class="something">
      <h4>投放区域：</h4>
      <el-select v-model="areaValue" placeholder="请选择投放区域">
        <el-option v-for="item in areas" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
      <h4>时间段：</h4>
      <el-select v-model="timeValue" placeholder="请选择时间段">
        <el-option v-for="item in times" :key="item.value" :label="item.label" :value="item.value"></el-option>
      </el-select>
      <br>
      <el-button type="primary" plain @click="showValue">确认</el-button>
    </div>
  </div>
</template>

<script>
  import {loadModules} from 'esri-loader';

  export default {
    name: 'trace',
    data () {
      return {
        areas: [
          {
            value: "选项1",
            label: "中南财经政法大学（南湖校区）"
          }
        ],
        times: [
          {
            value: "选项1",
            label: "0:00-2:00"
          },
          {
            value: "选项2",
            label: "2:00-4:00"
          },
          {
            value: "选项3",
            label: "4:00-6:00"
          },
          {
            value: "选项4",
            label: "6:00-8:00"
          },
          {
            value: "选项5",
            label: "8:00-10:00"
          },
          {
            value: "选项6",
            label: "10:00-12:00"
          },
          {
            value: "选项7",
            label: "12:00-14:00"
          },
          {
            value: "选项8",
            label: "14:00-16:00"
          },
          {
            value: "选项9",
            label: "16:00-18:00"
          },
          {
            value: "选项10",
            label: "18:00-20:00"
          },
          {
            value: "选项11",
            label: "20:00-22:00"
          },
          {
            value: "选项12",
            label: "22:00-0:00"
          },
        ],
        areaValue:'',
        timeValue:'',
        center_x:114.38099,
        center_y:30.47706,
        xmin:114.37747276,
        xmax:114.3871343,
        ymin:30.47493417,
        ymax:30.47600144,
      }
    },
    methods:{
      // 创建地图
      createView(){
        // 调用部署地址
        let options = {
          url:'https://js.arcgis.com/4.12/',
          css:'https://js.arcgis.com/4.12/esri/themes/light/main.css'
        };
        // 调用功能模块，创建地图
        loadModules(["esri/Map","esri/views/MapView","esri/layers/WebTileLayer"],options).then(([Map, MapView,WebTileLayer])=>{

          let tiledLayer = new WebTileLayer({
          urlTemplate:
            "http://{subDomain}.tianditu.gov.cn/DataServer?T=vec_w&x={col}&y={row}&l={level}&tk=9afdf2956e9d3cd48e3136dc4b122b21",
          subDomains: ["t0", "t1", "t2", "t3","t4", "t5", "t6", "t7"],
        });
 
          let tiledLayer_poi = new WebTileLayer({
            urlTemplate:
              "http://{subDomain}.tianditu.gov.cn/DataServer?T=cva_w&x={col}&y={row}&l={level}&tk=9afdf2956e9d3cd48e3136dc4b122b21",
            subDomains: ["t0", "t1", "t2", "t3","t4", "t5", "t6", "t7"],
          });
 
          let map = new Map({
              basemap: {
                baseLayers: [tiledLayer, tiledLayer_poi]
              }
          });

          this.view = new MapView({
          container: "trace-view",
          map: map,
          zoom: 15,
          center: [this.center_x,this.center_y], // longitude, latitude
          extent:{
            xmin:this.xmin,
            xmax:this.xmax,
            ymin:this.ymin,
            ymax:this.ymax,
            SpatialReference:{ wkid:4326 }
          }
        });
          this.isShow = !this.isShow;

        })

      },
      showValue(){
        console.log("hhh",this.timeValue);
      }
  },
  mounted(){
    this.createView();
  }
}
</script>

<style scoped>
#trace{
  background-image: url(../assets/blank.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  height: 750px;
}
#trace-view{
  position: absolute;
  width: 80%;
  height: 500px;
  left: 10%;
}
.areas{
  display: flex;
  flex-direction: row;
  margin: 20px;
}
.something{
  position: absolute;
  bottom: 30px;
  left: 30%;
}

h4{
  margin:0;
  padding-left:40px;
  display: inline-block;
}
.el-button{
  margin-top: 30px;
  width: 150px;
  font-size: 18px;
}
</style>