<template>
  <div id="plan">
    <Header />
    <h3>当前城市：{{ city }}</h3>
    <!-- <el-button type="button" round @click="confirm">确定</el-button> -->
    <div id="city"></div>
    <div class="steps">
      <el-steps :active="active" align-center finish-status="success">
        <el-step title="选择投放区域"></el-step>
        <el-step title="确定投放点"></el-step>
        <el-step title="选择调度方案"></el-step>
        <el-step title="生成投放策略"></el-step>
      </el-steps>
      <el-button @click="next">下一步</el-button>
    </div>
  </div>
</template>

<script>
import Header from "../components/Header.vue";
import {loadModules} from 'esri-loader';

export default {
  name: "Plan",
  data() {
    return {
      city:'武汉',
      center_x:114.1,
      center_y:30,
      xmin:113.73,
      xmax:115.05,
      ymin:29.58,
      ymax:31.22,
      active: 0
      }
  },
  components: {
    Header
  },
  methods: {
      createView(){
        // 调用部署地址
        let options = {
          url:'https://js.arcgis.com/4.12/',
          css:'https://js.arcgis.com/4.12/esri/themes/light/main.css'
        };
        // 调用功能模块，创建地图
        loadModules(["esri/Map","esri/views/MapView","esri/layers/WebTileLayer"],options).then(([Map, MapView,WebTileLayer])=>{

          let tiledLayer = new WebTileLayer({
          urlTemplate:
            "http://{subDomain}.tianditu.gov.cn/DataServer?T=vec_w&x={col}&y={row}&l={level}&tk=9afdf2956e9d3cd48e3136dc4b122b21",
          subDomains: ["t0", "t1", "t2", "t3","t4", "t5", "t6", "t7"],
        });

          let tiledLayer_poi = new WebTileLayer({
            urlTemplate:
              "http://{subDomain}.tianditu.gov.cn/DataServer?T=cva_w&x={col}&y={row}&l={level}&tk=9afdf2956e9d3cd48e3136dc4b122b21",
            subDomains: ["t0", "t1", "t2", "t3","t4", "t5", "t6", "t7"],
          });
 
          let map = new Map({
              basemap: {
                baseLayers: [tiledLayer, tiledLayer_poi]
              }
          });

          this.view = new MapView({
          container: "city",
          map: map,
          zoom: 15,
          center: [this.center_x,this.center_y], // longitude, latitude
          extent:{
            xmin:this.xmin,
            xmax:114.3871343,
            ymin:30.47493417,
            ymax:30.47600144,
            SpatialReference:{ wkid:4326 }
          }
        });
          // this.isShow = !this.isShow;

        })
      },
      // 关闭地图
      destroyView(){
        if (this.view) {
        // destroy the map view
        this.view.container = null;
        // this.isShow = !this.isShow;
      }
    },
    next(){
      if (this.active++ > 3) this.active = 0;
    }
  },
  mounted(){
    this.createView();
  }
}
</script>

<style scoped>
#plan{
  background-image: url(../assets/selectCity.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100% 100%;
  height: 810px;
  text-align: center;
}

.steps{
  position: absolute;
  width: 80%;
  left: 10%;
  bottom: 5px;
}

#city{
  position: absolute;
  width: 55%;
  left: 20%;
  height: 65%;
}
</style>